.info-bar {
    font-size: 16px;
    height: 20px;
    background-color: $mine-shaft;
    width: 100%;
}

.thumbnail-view{
    full-size-display-cmp{
        display:none;
    }
}
full-size-display-cmp {
    position: fixed;
    top: 30px;
    left: 300px;
    width: calc(100% - 330px);
    height: calc(100% - 30px);
    .display-content{
        height: calc(100% - 30px);
        img, video {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
        .img-polyfill {
            object-fit: contain;
            font-family: 'object-fit: contain;'
        }
    }
    video-cmp still-cmp{
        height:100%;
        width: 100%;
        display:block;
    }
    .display-order{
        margin: 0 0 10px 0;
        span:first-child{
            margin-left: 10px;
        }
    }
    .single-view {
        height:100%;
        lost-utility: clearfix;
        single-display-cmp {
            .display-info-bar {
                @extend .info-bar
            }
        }
    }
    .dual-view {
        height: 100%;
        lost-utility: clearfix;
        single-display-cmp {
            height: 100%;
            display:block;
            lost-column: 1/2 30px;
            .display-info-bar {
                @extend .info-bar
            }
        }
    }
    .quad-view {
        height: 100%;
        lost-utility: clearfix;
        single-display-cmp {
            lost-waffle: 1/2 2 5px;
            .display-info-bar {
                @extend .info-bar
            }
            .display-content {
                height: calc(100% - 20px);
                lost-align: center;
                still-cmp, video-cmp {
                    height: 100%;
                    width: 100%;
                }
            }
        }
    }
}