.playerbuttons{
    float:left;
}
.scenariobuttons{
    float: right;
}

button:focus {
    outline: 0;
}

.hide-nav {
    display: none;
}

.viewer-main-nav {
    position:fixed;
    top:0;
    left:0;
    color: $alto;
    width: 100%;
    height: 28px;
    background-color: $mine-shaft;

    scenariocontrol-cmp{
        display: inline-block;
        position: relative;
        top: 0;
        left: 0;
        background-color: $mine-shaft;
        width: 269px;
        height: 27px;
        lost-utility: clearfix;
        lost-align: center;
    }
    .scenario-control{
        height: inherit;
        width: 180px;
    }
    thumbnav-cmp{
        position: absolute;
        top:0;
        left: calc(50% + 232px);
        .icon{
            padding: 3px;
            font-size: 16px;
            height: 27px;
            width: 42px;
            &.selected {
                 color: $alto;
            }
        }
    }
    playercontrol-cmp{
        position: absolute;
        top:0;
        left: 50%;
        right: 0;
        width: 182px;
        height: 27px;
        border-radius: 2px;
        background-color: $mine-shaft;
        .icon{
            font-size: 12px;
            height: 27px;
            width: 36px;
            &.selected {
                color: $alto;
                &.icon-adjust{
                    color: $tahiti-gold;
                 }
            }
        &.icon-adjust{
             font-size: 20px;
        }
        }
    .player-control{
          height: inherit;
          width: 205px;
    }

    }

    .icon-adjust {
        margin-right: 40px;
    }


    .prev-button{
        margin-right: 3px;
    }
    .next-button, .prev-button{
        border-radius: 2px;
    }
    thumb-total-num-cmp {
        position: absolute;
        top: 6px;
        right: 30px;
        font-size: 16px;
    }
}