ul {
    list-style-type: none;
    margin: 0;
    &.thumbnailList-div {
         border: 1px solid #999999;
         background-color: black;
         padding-left:0;
     }
}

.thumbnailList-div::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: $mine-shaft;
}
.thumbnailList-div::-webkit-scrollbar
{
    width: 12px;
}
.thumbnailList-div::-webkit-scrollbar-thumb
{
    background-color: $gray;
}

.thumbnail-view{
    .thumbnailList-div{
        width:calc(100% - 10px);
        text-align: left;
        margin-left: 10px;
        border: none;
    }
}
/* sidebar that contains the thumbnail list */
.thumbnailList-div {
    /*max-width: 17%; !* from above: this is roughly 2-col on a lg or xl device *!*/
    min-width: $thumbnail-width+6; /* The 6 here refers to my note about padding in line 41 */
    border: $thumbnail-border;
    overflow-y: auto;
    overflow-x: hidden;
    position: fixed;
    top: 29px;
    left: 0;
    height: calc(100% - 29px);
    width: 270px;
    text-align: center;
    /*width: 200px; !* temporary for styling purposed*!*/
    .thumbnail-div{
        width: 250px;
        text-align: center;
        border: solid $heavy-outline $black;
        display: inline-block;
        margin-bottom: 8px;
        img{
            max-width:250px;
            max-height:250px;
            /*&:hover{*/
                 /*border: solid $standard-outline $tahiti-gold-opaque;*/
                 /*!*filter: opacity(.5);*!*/
                 /*!*filter: blur(2px);*!*/
                /*!*padding: 20px;*!*/
                /*!*width:210px;*!*/
             /*}*/
        }

        &:active{
             border: solid $heavy-outline $tahiti-gold;
        }
        &.thumbnail-click{
             border: solid $heavy-outline $tahiti-gold;
         }
        &:hover{
             border: solid $heavy-outline $alto;
             /*filter: opacity(.5);*/
             /*filter: blur(10px);*/
         }
        font-size: 16px;
    }
}

