
.adjust-dialog{
    z-index: 99999;
    position: absolute;
    top: 40px;
    left: 37vw;
    width: 328px;
    background-color: $cod-gray;
    border: solid 2px $alto;
    color: $alto;
    padding: 20px;
    border-radius: 2px;
    .slider-label{
        display: inline-block;
        width: 120px;
        font-size: 2.0em;
    }
    .slider-cmp{
        display: inline-block;
    }
    input{
        width: 200px;
    }
}

.brightness-contrast-reset{
    float: right;
    font-size: 14px;
    cursor: pointer;text-align: center;
    padding: 5px;
    border-radius: 2px;
    &:hover {
         background-color: $dove-gray;
     }
    &:active {
         color: $gray;
     }
}
